import Vue from 'vue'
import VueRouter from 'vue-router'
// import films from '@/views/films'
// import cinemas from '@/views/cinemas'
// import center from '@/views/center'
// import comingSoon from '@/views/comingSoon'
// import nowPlaying from '@/views/nowPlaying'
// import detail from '@/views/detail'
Vue.use(VueRouter)

const routes = [
  {
    path: '/films',
    name: 'films',
    // component: films,
    // webpack 代码分割
    component: () => import(/* webpackChunkName:'films' */'@/views/films'),
    children: [
      {
        path: 'nowPlaying',
        name: 'nowPlaying',
        // component: nowPlaying,
        component: () => import(/* webpackChunkName:'nowPlaying' */'@/views/nowPlaying')
      },
      {
        path: 'comingSoon',
        name: 'comingSoon',
        // component: comingSoon,
        component: () => import(/* webpackChunkName:'comingSoon' */'@/views/comingSoon')
      },
      {
        // 重定向路由，当路由到'/films'，再重定向到'/films/nowPlaying'
        path: '/films',
        redirect: '/films/nowPlaying'
      }

    ]
  },
  {
    path: '/detail/:id',
    name: 'detail',
    // component: detail,
    component: () => import(/* webpackChunkName:'detail' */'@/views/detail')
  },
  {
    path: '/cinemas',
    name: 'cinemas',
    // component: cinemas,
    component: () => import(/* webpackChunkName:'cinemas' */'@/views/cinemas')
  },
  {
    path: '/center',
    name: 'center',
    // component: center
    component: () => import(/* webpackChunkName:'center' */'@/views/center')
  },
  {
    // 重定向路由，
    path: '/',
    redirect: '/films/nowPlaying'
  }
]

const router = new VueRouter({
  routes
})

export default router
