<template>
  <div class='app'>
    <tabbar v-if='isShow2'></tabbar>
    <!-- tabbar引入在该模块中，点击下方对应的选项，得到路由，由下方的路由容器接收，并跳转到路由对应的页面 -->
    <!-- 路由容器，(基于slot的封装，相当于插槽接收内容) -->
    <router-view></router-view>
  </div>
</template>
<script>
import tabbar from './components/Tabbar'
export default {
  data () {
    return {
      isShow2: true
    }
  },
  components: {
    tabbar
  },
  watch: {
    // 实时获取路由信息，name
    $route: function (route) {
      // 当前路由name是detail详情页路由名时，底部Tabbar隐藏
      // console.log(route.name)
      if (route.name === 'detail') {
        this.isShow2 = false
      } else {
        this.isShow2 = true
      }
    }
  }
}
</script>
